
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
.mo-modal__wrapper .mo-modal.mo-modal--alert .mo-modal__body {
  font-size: 1rem;
}
.mo-modal__wrapper .mo-modal.mo-modal--confirm .mo-modal__body {
  font-size: 1rem;
}
.mo-modal__wrapper .mo-modal.mo-modal--toast {
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  border-radius: 2px;
  min-width: 6rem;
  width: auto;
  text-align: center;
}
.mo-modal__wrapper .mo-modal.mo-modal--toast .mo-modal__body {
    padding: 0.75rem 1.25rem;
}



