
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
.demo-panel {
  margin-top: 2rem;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
}
.demo-panel .demo-panel__header {
    padding: 1rem;
    border-bottom: 1px solid #d3d3d3;
}
.demo-panel .demo-panel__header .title {
      margin: 0;
      padding-left: 1rem;
      border-left: 4px solid #0275d8;
}
.demo-panel .demo-panel__body {
    padding: 1rem;
}



