
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
.mo-paging {
  display: inline-block;
  padding: 0;
  margin: 1rem 0;
  font-size: 0;
  list-style: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.mo-paging > .paging-item {
    display: inline-block;
    font-size: 0.875rem;
    position: relative;
    height: 2rem;
    line-height: 1.25rem;
    padding: 0.3125rem;
    min-width: 2rem;
    text-align: center;
    text-decoration: none;
    border: 1px solid #d3d3d3;
    background-color: #fff;
    margin-left: -1px;
    cursor: pointer;
    color: #515151;
}
.mo-paging > .paging-item i {
      position: relative;
      font-size: 1.125rem;
      vertical-align: middle;
      top: -0.0625rem;
}
.mo-paging > .paging-item:first-child {
      margin-left: 0;
}
.mo-paging > .paging-item:hover {
      background-color: #f0f0f0;
      color: #0275d8;
}
.mo-paging > .paging-item.paging-item--disabled, .mo-paging > .paging-item.paging-item--more {
      background-color: #fff;
      color: #515151;
}
.mo-paging > .paging-item.paging-item--disabled {
      cursor: not-allowed;
      opacity: .75;
}
.mo-paging > .paging-item.paging-item--more, .mo-paging > .paging-item.paging-item--current {
      cursor: default;
}
.mo-paging > .paging-item.paging-item--current {
      background-color: #0275d8;
      color: #fff;
      position: relative;
      z-index: 1;
      border-color: #0275d8;
}
.mo-paging .paging-item__select {
    display: inline-block;
    border: none;
    margin-left: 1rem;
    vertical-align: top;
}
.mo-paging .paging-item__select select {
      height: 2rem;
}



