
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
html.mo-modal__opened {
  overflow: hidden !important;
}
.mo-modal__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 19900206;
  text-align: center;
}
.mo-modal__wrapper > .mo-modal {
    display: inline-block;
    background-color: #fff;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
    margin-bottom: 3rem;
    min-width: 420px;
    max-width: 80%;
    max-height: 80%;
    overflow: hidden;
    margin-top: 15%;
    position: relative;
    text-align: left;
}
.mo-modal__wrapper > .mo-modal > .mo-modal__header {
      padding: 1.25rem 1.25rem 0;
      line-height: 1;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
}
.mo-modal__wrapper > .mo-modal > .mo-modal__body {
      padding: 1.875rem 1.25rem;
}
.mo-modal__wrapper > .mo-modal > .mo-modal__footer {
      padding: 0 1.25rem 1.25rem;
      text-align: right;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
}
.mo-modal__wrapper > .mo-modal .mo-modal__title {
      font-size: 1.25rem;
      line-height: 1;
      margin: 0;
      padding: 0;
}
.modal-fade-enter-active .mo-modal,
.modal-fade-leave-active .mo-modal {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.modal-fade-enter-active {
  -webkit-animation: modalFadeIn .26s;
          animation: modalFadeIn .26s;
}
.modal-fade-leave-active {
  -webkit-animation: modalFadeOut .2s;
          animation: modalFadeOut .2s;
}
@-webkit-keyframes modalFadeIn {
0% {
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes modalFadeIn {
0% {
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@-webkit-keyframes modalFadeOut {
0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    -webkit-transform: translate3d(0, -10px, 0);
            transform: translate3d(0, -10px, 0);
    opacity: 0;
}
}
@keyframes modalFadeOut {
0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    -webkit-transform: translate3d(0, -10px, 0);
            transform: translate3d(0, -10px, 0);
    opacity: 0;
}
}
@media screen and (max-width: 768px) {
.mo-modal__wrapper .mo-modal {
    min-width: auto;
    width: 80%;
}
}



