
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
.app-aside {
  position: fixed;
  width: 15rem;
  top: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.117647), 0 1px 4px rgba(0, 0, 0, 0.117647);
}
.app-aside::-webkit-scrollbar {
  display: none !important;
  width: 0 !important;
  height: 0 !important;
  -webkit-appearance: none;
  opacity: 0 !important;
}
.app-aside__nav {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.app-aside__nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.app-aside__nav ul li.active {
      background-color: #0275d8;
}
.app-aside__nav ul li.active a {
        color: #fff;
}



