
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
.icon-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  margin: 2rem;
  padding: 1px 0 0 1px;
  list-style: none;
}
.icon-list .icon-item {
    width: 6rem;
    height: 5rem;
    border: 1px solid #d3d3d3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-top: -1px;
    margin-left: -1px;
    position: relative;
    background-color: #fff;
    cursor: zoom-in;
}
.icon-list .icon-item small {
      margin-top: .5rem;
}
.icon-list .icon-item:hover {
      -webkit-transition: all .12s;
      transition: all .12s;
      z-index: 1;
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
      background-color: #0275d8;
      border-color: #0275d8;
      color: #fff;
      box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
      cursor: default;
}



