
@charset "UTF-8";
/**
 * 去掉数值的单位
 * stripUnits(16px) => 16
 */
/**
 * 多数值转rem
 * 用于padding / margin等
 * eg. rem(16 32 16 16) => 1rem 2rem 1rem 1rem
 */
/**
 * 清除浮动
 */
.mo-number-input {
  display: inline-block;
  margin: 0;
  padding: 0;
  position: relative;
  text-align: center;
}
.mo-number-input input {
    width: 6rem;
    text-align: center;
    padding-left: .25rem;
    padding-right: .25rem;
    -webkit-transition: padding-right .2s;
    transition: padding-right .2s;
}
.mo-number-input .mo-number__control {
    position: absolute;
    width: 1rem;
    top: 0;
    bottom: 0;
    right: 0;
    font-size: 1rem;
    margin: 1px;
    opacity: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}
.mo-number-input .mo-number__control .mo-number__plus,
    .mo-number-input .mo-number__control .mo-number__minus {
      width: 1rem;
      height: 50%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-flow: column;
              flex-flow: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      color: #9e9e9e;
}
.mo-number-input .mo-number__control .mo-number__plus:hover,
      .mo-number-input .mo-number__control .mo-number__minus:hover {
        color: #0275d8;
        background-color: #f0f0f0;
}
.mo-number-input .mo-number__control .mo-number__plus[disabled],
      .mo-number-input .mo-number__control .mo-number__minus[disabled] {
        color: #9e9e9e !important;
        opacity: .5;
        cursor: default;
        background-color: none;
}
.mo-number-input:hover input {
    padding-right: 1rem;
}
.mo-number-input:hover .mo-number__control {
    opacity: 1;
}



