
.dropmenu {
  position: relative;
}
.dropmenu a {
    display: block;
    line-height: 24px;
    padding: 8px 16px;
    text-decoration: none;
}
.dropmenu > .dropmenu-target {
    font-size: 1rem;
    position: relative;
    color: #333;
}
.dropmenu > .dropmenu-menu {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
    display: none;
}
.dropmenu > .dropmenu-menu li a {
      font-size: 14px;
      color: #666;
      text-decoration: none;
      padding-left: 2rem;
}
.dropmenu.dropmenu-open > .dropmenu-menu {
    display: block;
}



