.app {
  padding-left: 15rem;
}
.app .app-main {
    padding: 4rem 1rem;
    position: relative;
}
.app .app-main .app-scroll {
      position: relative;
      overflow: auto;
}
.app .app-main .app-scroll:before, .app .app-main .app-scroll:after {
        display: table;
        content: ' ';
}
.app .app-main .app-scroll:after {
        clear: both;
}
.app .app-main .demo-name {
      font-size: 2rem;
}
.app .app-main .demo-title {
      font-size: 1.25rem;
      margin-top: 3rem;
      color: #d9534f;
}
.app .app-main .demo-title i {
        font-size: 1.5rem;
        vertical-align: middle;
        position: relative;
        top: -0.125rem;
        margin-right: 0.3125rem;
}
.app .app-aside {
    z-index: 11;
    background-color: #fff;
}
.app .app-header {
    position: fixed;
    left: 0;
    right: 0;
    height: 3rem;
    background-color: rgba(255, 255, 255, 0.75);
    z-index: 10;
    top: 0;
    padding: 0.625rem 1.25rem 0.625rem 16.25rem;
    line-height: 28px;
    font-size: 1rem;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
}
.app .app-header iframe {
      display: inline;
      max-width: 6rem;
      margin-top: 5px;
}



